import { useState } from "react";

import AddToCartBtnBg from "../../../assets/icons/AddToCartBtnBg";

import styles from "./ProductInfo.module.sass";

const ProdictInfo = ({ id, name, price, desc, details }) => {
  const [selectedSize, setSelectedSize] = useState("XSS");
  const [btnText, setBtnText] = useState(" ");

  const handleSizeChange = (e) => {
    setSelectedSize(e.target.value);
  };

  const handleAddProductToCartClick = (e) => {
    e.preventDefault();
    const arr = localStorage.getItem("arrOfCartProducts")
      ? JSON.parse(localStorage.getItem("arrOfCartProducts"))
      : [];
    if (arr.length === 0) {
      localStorage.setItem(
        "arrOfCartProducts",
        JSON.stringify([...arr, { id, selectedSize }])
      );
    }
    for (let item of arr) {
      if (item.id === id) {
        let repeat = false;
        for (let item of arr) {
          if (item.selectedSize === selectedSize && item.id === id) {
            repeat = true;
            break;
          }
        }
        if (!repeat) {
          localStorage.setItem(
            "arrOfCartProducts",
            JSON.stringify([...arr, { id, selectedSize }])
          );
          setBtnText("!");
          setTimeout(() => {
            setBtnText((prevText) =>
              prevText === "!" ? " " : prevText
            );
          }, 2000);
        }
      } else {
        if (arr.every((item) => item.id !== id)) {
          localStorage.setItem(
            "arrOfCartProducts",
            JSON.stringify([...arr, { id, selectedSize }])
          );
          setBtnText("!");
          setTimeout(() => {
            setBtnText((prevText) =>
              prevText === "!" ? " " : prevText
            );
          }, 2000);
        }
      }
    }
  };

  return (
    <div className={styles.productInfo}>
      <h1 className={styles.name}>{name}</h1>
      <p className={styles.price}>{price} &#8381;</p>
      <form action="" className={styles.form}>
        <div className={styles.size}>
          <p className={styles.chooseSize}> :</p>
          <div action="" className={styles.sizeOptions}>
            <label htmlFor="XSS">
              <input
                type="radio"
                id="XSS"
                name="size"
                value="XSS"
                checked={selectedSize === "XSS"}
                onChange={handleSizeChange}
              />
              <span>XS S</span>
            </label>
            <label htmlFor="SM">
              <input
                type="radio"
                id="SM"
                name="size"
                value="SM"
                checked={selectedSize === "SM"}
                onChange={handleSizeChange}
              />
              <span>S M</span>
            </label>
            <label htmlFor="LXL">
              <input
                type="radio"
                id="LXL"
                name="size"
                value="LXL"
                checked={selectedSize === "LXL"}
                onChange={handleSizeChange}
              />
              <span>L XL</span>
            </label>
            <label htmlFor="ML">
              <input
                type="radio"
                id="ML"
                name="size"
                value="ML"
                checked={selectedSize === "ML"}
                onChange={handleSizeChange}
              />
              <span>M L</span>
            </label>
          </div>
        </div>
        <div
          className={
            styles.svgButton +
            " " +
            (btnText === "!" ? styles.disabled : "")
          }
        >
          <AddToCartBtnBg />
          <button
            className={styles.buttonText}
            onClick={handleAddProductToCartClick}
          >
            {btnText}
          </button>
        </div>
      </form>

      <p className={styles.description}>{desc}</p>
      <div className={styles.dottedLine}></div>
      <p className={styles.madeOf}>
        <b>: </b>
        {details}
      </p>
    </div>
  );
};

export default ProdictInfo;
